<script>
    export default {
        name: 'HomePage',
        data () {
            return {}
        },
        mounted: function() {
            $(document).ready(function () {
                $('.bxslider').bxSlider({
                    auto: false,
                    speed: 600,
                    pause: 12000,
                });
            });

            window.onresize = function (event) {
                responsiveNavbar();
                ResponsiveSlider();
            };

            window.onload = function (event) {
                responsiveNavbar();
                ResponsiveSlider();
            };

            // Responsive when document ready
            $(function () {
                responsiveNavbar();
                ResponsiveSlider();
            });

            function responsiveNavbar() {
                var ck = window.matchMedia("(min-width: 991px) and (max-width: 1024px)");
                var dk = window.matchMedia("(min-width: 1025px)");
                if (dk.matches) {
                    $('.navbar').css({ 'position': 'absolute', 'left': '12%' });
                }
                if (ck.matches) {
                    $('.navbar').css({ 'position': 'absolute', 'left': '4%' });
                }
            };

            function ResponsiveSlider() {
                var xs = window.matchMedia("(max-width: 767px)");
                var sm = window.matchMedia("(min-width: 768px) and (max-width: 991px)");
                if (sm.matches) {
                    $('.bxslider1').bxSlider({
                        slideWidth: 5000,
                        minSlides: 4,
                        auto: false,
                        speed: 600,
                        pause: 3000,
                        pager: false,
                        maxSlides: 4,
                        slideMargin: 20
                    });
                }
                if (xs.matches) {
                    $('.bxslider1').bxSlider({
                        slideWidth: 5000,
                        minSlides: 2,
                        auto: false,
                        speed: 600,
                        pause: 3000,
                        pager: false,
                        maxSlides: 2,
                        slideMargin: 20
                    });
                } else {
                    $('.bxslider1').bxSlider({
                        slideWidth: 5000,
                        minSlides: 4,
                        auto: false,
                        speed: 600,
                        pause: 3000,
                        pager: false,
                        maxSlides: 4,
                        slideMargin: 20
                    });
                }
            };
        }
    }
</script>



